.container {
  //display: flex;
  //justify-content: space-between;
  margin: 0 auto;
  padding: 0 15px;
  @media (min-width: 544px) {
    max-width: 576px;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    max-width: 940px;
  }
  @media (min-width: 1200px) {
    max-width: 1140px;
  }
  .row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    .colLeft{
      position: relative;
      min-height: 1px;
      padding-right: 15px;
      padding-left: 15px;
      flex: 0 0 75%;
      max-width: 75%;
    }
    .colRight {
      position: relative;
      min-height: 1px;
      padding-right: 15px;
      padding-left: 15px;
      flex: 0 0 25%;
      max-width: 25%;
    }
  }
}
